import React from 'react'


export type ContextProps = {
    app: Realm.App<globalThis.Realm.DefaultFunctionsFactory, any> | null,
    user: Realm.User | null,
    setApp: any,
    setUser: any
}

const initialContext: ContextProps = {
    app: null,
    user: null,
    setApp: () => {},
    setUser: () => {}
}

const MongoContext = React.createContext<ContextProps>(initialContext)

export default MongoContext