import Modal from "../modal/modal"
import {FC, useContext, useState} from 'react'
import styles from './editsong.module.css'
import { Rating } from "../rating/rating";
import { KTab } from "../../models/KTab";
import MongoContext from "../../context/MongoContext";
import { BSON } from "realm-web";
import { Config } from "../../Config";

type Props =  {
    open: boolean,
    onClose: (reload: boolean) => void,
    toEditTitle: string,
    toEditInterpreter: string,
    toEditSource: string,
    toEditYoutube: string,
    toEditSongtext: string,
    toEditDifficulty: number
    id: string,
}

const EditSong: FC<Props> = ({open, onClose, toEditTitle, toEditInterpreter, toEditSource, toEditYoutube, toEditSongtext, toEditDifficulty, id}) => {

  const state = useContext(MongoContext);

  const [title, setTitle] = useState(toEditTitle)
  const [interpreter, setInterpreter] = useState(toEditInterpreter)
  const [source, setSource] = useState(toEditSource)
  const [youtube, setYoutube] = useState(toEditYoutube)
  const [text, setText] = useState(toEditSongtext)
  const [difficulty, setDifficulty] = useState(toEditDifficulty)

  /* function to update firestore */
  const handleUpdate = async (e: any) => {
    e.preventDefault()

    if ( !title || title.trim().length === 0 ) {
      alert('Enter title text');
      return;
    }
    if ( !text || text.trim().length === 0 ) {
      alert('Enter song text');
      return;
    }
    
    const kTab: Partial<KTab> = {
      title: title,
      interpreter: interpreter,
      source: source,
      youtube: youtube,
      text: text,
      difficulty: difficulty,
      updated: new Date()
    }
    
    if (state.app?.currentUser ) {
      const rests = state.app?.currentUser?.mongoClient(Config.clustername).db(Config.dbName).collection(Config.collection)
      console.log('update', state.app?.currentUser);
      
      rests.updateOne({"_id": new BSON.ObjectID(id)}, kTab)
        .then (()=>{
          onClose(true)
        })
        .catch ((err) => {
          alert(err)
          onClose(false)
        })
    }
    
  }

  return (
    <Modal modalLabel='Edit Kalimba Tab' onClose={onClose} open={open}>
      <Rating title="Set difficulty: " rating={difficulty} onChange={setDifficulty} />
      <form onSubmit={handleUpdate} className={styles.editSong}>
        <input 
          type='text' 
          name='title' 
          placeholder="title"
          onChange={(e) => setTitle(e.target.value)} 
          value={title}/>
        <input 
          type='text' 
          name='interpreter' 
          placeholder="interpreter"
          onChange={(e) => setInterpreter(e.target.value)} 
          value={interpreter}/>
          
        <textarea 
        onChange={(e) => setText(e.target.value)}
        placeholder='Enter song text'
        value={text}>{text}</textarea>

        <input 
          type='url' 
          name='source' 
          placeholder="source"
          onChange={(e) => setSource(e.target.value)} 
          value={source}/>
        <input 
          type='url' 
          name='youtube' 
          placeholder="youtube link"
          onChange={(e) => setYoutube(e.target.value)} 
          value={youtube}/>

        <button type='submit'>Save</button>
      </form> 
    </Modal>
  )
}

export default EditSong