import {FC, useContext, useEffect, useRef, useState} from 'react'
import styles from './addsong.module.css'
import Modal from './../modal/modal'
import { Rating } from '../rating/rating'
import MongoContext from '../../context/MongoContext'
import { KTab } from '../../models/KTab'
import { Config } from '../../Config'


type AddSongProps = {
  onClose: (reload: boolean) => void,
  open: boolean
}

const AddSong : FC<AddSongProps> = ({onClose, open}) => {

  const state = useContext(MongoContext);
  const titleInput = useRef<any>(null);

  useEffect(()=>{
     // current property is refered to input element
     if ( titleInput?.current ) titleInput.current.focus();
  },[])

  const [title, setTitle] = useState('')
  const [interpreter, setInterpreter] = useState('')
  const [source, setSource] = useState('')
  const [youtube, setYoutube] = useState('')
  const [difficulty, setDifficulty] = useState(0)
  const [text, setText] = useState('')

  const handleSubmit = (e: any) => {
    e.preventDefault()
    if ( !title || title.trim().length === 0 ) {
      alert('Enter title text');
      return;
    }
    if ( !text || text.trim().length === 0 ) {
      alert('Enter song text');
      return;
    }
    const kTab: Partial<KTab> = {
      title: title,
      interpreter: interpreter.trim(),
      source: source.trim(),
      youtube: youtube.trim(),
      text: text,
      difficulty: difficulty,
      updated: new Date()
    }
    if ( state.app?.currentUser?.isLoggedIn ) {
      const rests = state.app?.currentUser?.mongoClient(Config.clustername)?.db(Config.dbName).collection(Config.collection)
      rests.insertOne(kTab)
        .then (()=>{
          onClose(true)
        })
        .catch ((err) => {
          alert(err)
          onClose(false)
        })
    }
  }

  return (
    <Modal modalLabel='Add Kalimba Tabulature' onClose={onClose} open={open}>
      <Rating title="Set difficulty: " rating={difficulty} onChange={setDifficulty} />
      <form onSubmit={handleSubmit} className={styles.addSong} name='addSong'>
        <input 
          ref={titleInput}
          type='text' 
          name='title' 
          onChange={(e) => setTitle(e.target.value)} 
          value={title}
          placeholder='Enter title'/>
        <input 
          type='text' 
          name='interpreter' 
          onChange={(e) => setInterpreter(e.target.value)} 
          value={interpreter}
          placeholder='interpreter'/>
        <textarea 
          onChange={(e) => setText(e.target.value)}
          placeholder='Enter song text'
          value={text} />

        <input 
          type='url' 
          name='source' 
          onChange={(e) => setSource(e.target.value)} 
          value={source}
          placeholder='source'/>
        <input 
          type='url' 
          name='youtube' 
          onChange={(e) => setYoutube(e.target.value)} 
          value={youtube}
          placeholder='youtube'/>
        <button type='submit'>Save</button>
      </form> 
    </Modal>
  )
}

export default AddSong