import { FC, useMemo } from "react";
import styles from './pagination.module.css';

type Props = {
    currentPage: number,
    total: number,
    countPerPage: number,
    onChangePage: (page: number) => void,
}
export const Pagination: FC<Props> = ({currentPage, total, countPerPage, onChangePage}) => {

    const maxPage = useMemo(() => Math.floor(total / countPerPage), [total, countPerPage])

    const pages = useMemo(() => 
        { 
            const result = []
            const start = Math.max(0, currentPage - 3)
            const end = Math.min(maxPage, currentPage + 3)
            
        for (let p = start; p <= end; p++) {
            result.push(p)
        }
        return result
    }, [maxPage, currentPage]
    )


    return (
        <>
        { total > 0 ? 
        <div className={styles.pagination}>
            { currentPage > 0 ? <span onClick={() => onChangePage(currentPage-1)}>&laquo;</span> : '' }

            { pages.map((page) =>
                 <span key={'page-' + page} 
                    className={`${currentPage === page ? styles.active : ''}`}
                    onClick={() => onChangePage(page)}
                    >{page+1}</span> )
            }

            { currentPage < maxPage ? <span onClick={() => onChangePage(currentPage+1)}>&raquo;</span> : '' }
        </div> 
        : '' }
        </>
    )
}