import React, { useContext, useMemo } from 'react'
import { useEffect, useState } from 'react'
import { Button, Container, Form, ListGroup } from 'react-bootstrap'
import AddSong from '../components/addsong/addsong'
import { FindSong } from '../components/findsong/findsong'
import KTabCard from '../components/KTabCard'
import Loading from '../components/Loading'
import { Pagination } from '../components/pagination/pagination'
import { Config } from '../Config'
import { KTab } from '../models/KTab'
import MongoContext from '../context/MongoContext'
import { isReadOnly } from '../helpers/user'

const caseInsensitive = 'i';

type FunctionArgs = {
    filter: any,
    sort: any,
    limit: number,
    skip: number
}
type FunctionResult = {
    arg: any,
    data: KTab[]
}

function Home () {
    const state = useContext(MongoContext);
    const [kTabs, setKtabs] = useState<KTab[]>([])
    const [loading, setLoading] = useState(true)
    const [openAddModal, setOpenAddModal] = useState(false)
    const [searchText, setSearchText] = useState('')
    const [total, setTotal ] = useState(0)
    const [page, setPage ] = useState(0)
    const [filter, setFilter] = useState<any>({})
    const [reload, setReload] = useState(false)
    const [sortField, setSortField] = useState('title')
    const readOnly = useMemo(() => isReadOnly(state.user), [state.user]) 

    useEffect(() => {
        if ( state.app?.currentUser?.isLoggedIn ) {
            const rests = state.app?.currentUser?.mongoClient(Config.clustername).db(Config.dbName).collection(Config.collection)
            const regex = new RegExp(searchText,caseInsensitive) 
            
            let f = searchText.trim().length > 0 ? { "$or":  [ { title: {$regex: regex}}, { interpreter: {$regex: regex}}]} : {}
           
            setFilter(f)
            rests.count(f)
                .then((result) => setTotal(result))
                .catch((err) => console.log(err))
        }
    }, [searchText, state.app?.currentUser, reload])

    useEffect(() => {
        if ( state.user?.isLoggedIn ) {
            setLoading(true)
            let sort = {}
            switch (sortField) {
                case 'title':
                    sort = {title: 1, _id: 1}
                    break;
                case 'interpreter':
                    sort = {interpreter: 1, title: 1, _id: 1}
                    break;
                case 'difficulty':
                    sort = {difficulty: 1, title: 1, _id: 1}
                    break;
                case 'updated':
                    sort = {updated: -1, _id: 1}
                    break;
                case 'difficulty_desc':
                    sort = {difficulty: -1, title: 1, _id: 1}
                    break;
                default:
                    sort = {title: 1, _id: 1}
                    break;
            }
          
            const args: FunctionArgs = { skip: page * Config.pageSize, limit: Config.pageSize, filter: filter, sort: sort }
            state.user?.callFunction('skip_limit', args)
                .then((result) => {
                    setKtabs((result as FunctionResult).data)
                })
                .catch((err) => console.log(err))
                .finally(() => setLoading(false))
        }
    }, [state.user, filter, page, reload, sortField])

    const handleSearch = (t: string) => {
        setPage(0)
        setSearchText(t);
    }

    return (
        <Container>
            <FindSong onSearch={handleSearch} />
            {loading && (
                <div className="text-center">
                    <Loading />
                </div>
            )}
            
                <Container className='d-flex justify-content-between mt-3'>
                    <div>Total: {total}</div>
                    <div className='d-flex'>
                        <Form.Label>sort by </Form.Label>
                        <Form.Select aria-label="sort selection" size="sm" onChange={(e)=> setSortField(e.target.value)}>
                            <option value="title">title</option>
                            <option value="interpreter">interpreter</option>
                            <option value="updated">update time</option>
                            <option value="difficulty">difficulty</option>
                            <option value="difficulty_desc">difficulty desc</option>
                        </Form.Select>
                        { ! readOnly ? 
                            <Button size="sm" className='ms-3' variant='outline-dark' onClick={() => setOpenAddModal(true)} >
                                Add song
                            </Button> 
                        : '' }
                    </div>
                </Container> 
               

            <ListGroup as="ul" className='mt-3 mb-3'>
                {kTabs.map((ktab) => (
                    <KTabCard key={ktab._id} ktab={ktab} readOnly={readOnly} onReload={() => setReload(!reload)} />
                ))}
            </ListGroup>
            <Pagination currentPage={page} total={total} countPerPage={Config.pageSize} onChangePage={setPage}/>

            {   openAddModal &&
                <AddSong onClose={(r) => { setOpenAddModal(false); if (r) setReload(!reload)}} open={openAddModal}/>
            }
        </Container>
    )
}

export default Home