import React, { FC, useContext, useMemo, useState } from "react"
import { Nav, Navbar } from "react-bootstrap"
import MongoContext from "../context/MongoContext"
import { isReadOnly } from "../helpers/user"
import LogIn from "../pages/LogIn"


const Navigation: FC = () => {
    const state = useContext(MongoContext);
    const readOnly = useMemo(() => isReadOnly(state.user), [state.user]) 
    const [showLogin, setShowLogin] = useState(false)
    
    
    async function logout (e: any) {
        e.preventDefault()
        setShowLogin(false)
        if (state.app?.currentUser) {
            console.log('logout...');
            
            await state.app.currentUser.logOut().then(() => {
                console.log('successfull logged out');
                state.setUser(null)
            }
            ).catch((err)=> {
                console.log(err);
            })
        }
    }


    return (
        <>
            <Navbar bg="light" expand="lg">
                <Navbar.Brand href="/" className="mx-2">Tnx Kalimba Tabs</Navbar.Brand>

                <Nav className="ms-auto">
                    { readOnly ? 
                        <Navbar>
                            <Nav.Link href='# ' className="mx-2" onClick={() => setShowLogin(true)}>
                                Sign In
                            </Nav.Link>
                        </Navbar> 
                        : 
                        <Navbar>
                            <Navbar.Text>
                                {state.user?.profile.name || state.user?.profile.email}
                            </Navbar.Text>
                            <Nav.Link href='# ' className="mx-2" onClick={logout}>
                                Log out
                            </Nav.Link>
                        </Navbar> 
                    }
                </Nav>
            </Navbar>
            { showLogin ? <LogIn onLogin={() => setShowLogin(false) } /> : ''}
        </>
    )
}

export default Navigation