import React from "react"
import Home from "./pages/Home"
import 'bootstrap/dist/css/bootstrap.min.css'
import MongoContext from './context/MongoContext'
import { useEffect, useState } from "react"
import * as Realm from 'realm-web'
import Navigation from "./components/Navigation";
import { apiKeyUser } from "./helpers/user";

function App() {
  const [user, setUser] = useState<Realm.User | null>(null)
  const [app, setApp] = useState(new Realm.App({id: process.env.REACT_APP_REALM_APP_ID || ''}))

  useEffect(() => {
    async function init () {
      if (!user) {
        setUser(app.currentUser ? app.currentUser : await app.logIn(apiKeyUser())) 
      }
    }

    init();
  }, [user, app])

  return (
    <>
      <MongoContext.Provider value={{app, user, setUser, setApp}}>
        <Navigation />
        <Home />
      </MongoContext.Provider>
      
    </>

  );
}

export default App;
