import React, { FC, useContext } from "react"
import { Formik } from 'formik'
import { Button, Container, Form } from 'react-bootstrap'
import * as yup from 'yup'
import * as Realm from 'realm-web'
import { useState } from 'react'
import Loading from '../components/Loading'
import MongoContext from "../context/MongoContext"

const userSchema = yup.object().shape({
    email: yup.string().email().required(),
    password: yup.string().required()
})

type Props = {
    onLogin: () => void
}

const LogIn: FC<Props> = ({onLogin}) => {
    const state = useContext(MongoContext);
    const [loading, setLoading] = useState(false)

    async function submitHandler (values: any) {
        setLoading(true)

        //login user and redirect to home
        
        if (state.app) {
            console.log('login...');
            state.app.currentUser?.logOut().then(
                () =>{
                    const credentials = Realm.Credentials.emailPassword(values.email, values.password);
                    state.app?.logIn(credentials)
                    .then((user) => {
                        state.setUser(user);
                        console.log('logged in...');
                        onLogin()
                    })
                    .catch((err) => alert(err))
                }
            ).catch((err) => {
                console.log('can not logout...');
                console.log(err);
            })
        } else {
            state.setUser(null)
        }
        setLoading(false)
    }

    return (
        <Container className='d-flex justify-content-center mt-3'>
            <Formik
                initialValues={{
                    email: '',
                    password: ''
                }}

                validationSchema={userSchema}

                onSubmit={submitHandler}
            >
                {({errors, touched, handleSubmit, values, handleChange}) => (
                    <Form noValidate onSubmit={handleSubmit}>
                        {loading && <Loading />}
                        {!loading && (<div>
                            <h1>Login</h1>
                            <Form.Group>
                                <Form.Label>Email</Form.Label>
                                <Form.Control type="email" name="email" value={values.email} onChange={handleChange} 
                                isValid={touched.email && !errors.email} />
                                <Form.Control.Feedback>{errors.email}</Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" name="password" value={values.password} onChange={handleChange} 
                                isValid={touched.password && !errors.password} />
                                <Form.Control.Feedback>{errors.password}</Form.Control.Feedback>
                            </Form.Group>
                            <div className="text-center mt-2">
                                <Button variant="primary" type="submit">Submit</Button>
                            </div>
                        </div>)}
                    </Form>
                )}
            </Formik>
        </Container>
    )
}

export default LogIn