
import * as Realm from 'realm-web'

export const apiKeyUser = () =>  {
    const apiKey = process.env.REACT_APP_REALM_APIKEY_READALL;
    if (!apiKey) {
        throw new Error("Could not find a Realm Server API Key.");
    }
    return Realm.Credentials.apiKey(apiKey);
}

export const anonUser = () => {
    return Realm.Credentials.anonymous()
}

export const isReadOnly = (user: any) => {
    return !user || user.profile.name === "read_anonym"
}
